<template>
  <router-view/>
</template>

<script>

import axios from "axios"

export default {
  name: 'App',
  components: {
  },
  methods: {
    async getAccessToken() {
      await axios.post("https://us.battle.net/oauth/token?grant_type=client_credentials&client_id="+this.$store.state.accessData.clientID+"&client_secret="+this.$store.state.accessData.clientSecret, {})
        .then(result => {
          this.$store.state.accessData.accessToken = result.data.access_token
        })
    },
    getCharacterDataValidation() {
      this.$store.state.charactersData.map((item , index) => {
        this.$store.state.charactersData[index].realm = item.realm.toLowerCase().replace(/ /g, '-').replace(/'/g, '')
        this.$store.state.charactersData[index].name = item.name.toLowerCase()
        this.$store.state.charactersData[index].nameSpace = "profile-" + item.region
        this.$store.state.charactersData[index].nameSpaceStatic = "static-" + item.region
        this.$store.state.charactersData[index].nameSpaceDynamic = "dynamic-" + item.region
      })
    },
    getCharacterInfo() {
      this.$store.state.charactersData.map(( _ , index) => {
        axios.get("https://"+this.$store.state.charactersData[index].region+".api.blizzard.com/profile/wow/character/"+this.$store.state.charactersData[index].realm+"/"+this.$store.state.charactersData[index].name+"?namespace="+this.$store.state.charactersData[index].nameSpace+"&locale="+this.$store.state.locale+"&access_token="+this.$store.state.accessData.accessToken)
          .then(result => {
            this.$store.state.charactersData[index].info = result.data
            console.log(result.data)
          });
      })
    },
    async accumulate() {
      await this.getAccessToken()
      this.getCharacterDataValidation()
      this.getCharacterInfo()
    }
  },
  mounted() {
   this.accumulate()
  }
}
</script>

<style lang="sass">
@use '@/sassModules/base'
@use '@/sassModules/character-list'
@use '@/sassModules/class'
@use '@/sassModules/variables'

